@import'../../scss/variable','../../scss/mixins';

.navbar{
    background-color: $cardNavBgColor;
    display: flex;
    justify-content: space-evenly;
    a{
        color: white;
        font-size: 2rem;
        text-decoration: none;
        padding: 1rem;
        &:hover{
            background-color: $backgroundColor;
            color: $cardNavBgColor;
            
        }
    }
}
@include media-xsm{
    .navbar{
        flex-direction: column;
        text-align: center;
    }
}

@include media-sm{
    .navbar{
        flex-direction: row;
        

    }
}